.complete {
    width: 80%;
    min-height: 65vh;
    padding: 4%;
    margin: 20px auto;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: var(--night-blue);
    text-align: center;
}
.complete img {
    display: block;
    width: 100%;
}
/* .complete h3 {
    font-weight: 400;
} */
.complete button {
    display: block;
    margin: 20px auto;
    padding: 4%;
    background: var(--red);
    border: none;
    border-radius: 15px;
    font-size: 1.2em;
    font-weight: 600;
    color: var(--white);
}
.complete span {
    padding: 1%;
    color: var(--red);
    font-size: 1.2em;
    border-bottom: 1px solid var(--red);
}
.complete .rates {
    margin-bottom: 30px;
}
.complete .rate img {
    display: block;
    width: 60%;
    margin: auto;
}
.complete .rate h5 {
    margin-bottom: 5px;
}


@media screen and (min-width: 992px) {
    .complete {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60%;
        text-align: left;
    }
    .complete img {
        width: 60%;
    }
    .complete button {
        margin: 20px 0px;
    }
    .complete span {
        cursor: pointer;
    }
    .complete h3 {
        text-align: left;
        margin-left: 0px;
    }
    .complete .rate h5 {
        margin: 5px;
    }
    .complete .rates {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: center;
    }
    .complete .rate {
        margin-right: 20px;
    }
    .complete .rate img {
        display: block;
        width: auto;
        height: 6vw;
    }
}