}

:root {
  --no-value: none;
  --white: #fff;
  --berge: #f6efef;
  --red: #cc1616;
  --red-transparent: #cc161693;
  --blue: blue;
  --night-blue: #0f394c;
  --transparent: #00000029;
  --transparent2: #a6a8a8;
  --white-gray: #f9f9f9;
  --gray: #707070;
  --black: #000000;
  --transparency: transparent;
  --police: 'Vollkorn', serif;
  --police2: 'Merienda', cursive;
}

html,
body,
header,
main,
footer,
ul {
  margin: 0px;
  padding: 0px;
}

html {
  scroll-behavior: smooth;
}
body {
}

.approot {
