  border-radius: 5px;
  color: var(--white);
  font-size: 1.2em;
  font-weight: 600;
}
.modal-controls button:nth-child(1) {
  background: var(--red);
}
.modal-controls button:nth-child(2) {
  background: var(--night-blue);
}
.wallet {
  font-size: 0.9em;
}
.send-bloc {
  border: 1px solid var(--night-blue);
