.notfound .error {
  display: flex;
  flex-flow: row wrap;
  width: 900px;
  height: 50vh;
  margin: 20vh auto;
}
.notfound .first {
  /* display: inline-block; */
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  width: 30%;
  height: 90%;
}
.notfound .image {
  width: 40vh;
  height: 40vh;
  border-radius: 50%;
  background-color: var(--berge);
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
}

.notfound .second {
  /* display: inline-block; */
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  width: 65%;
  height: 90%;
  margin-left: 3%;
  vertical-align: top;
}
.notfound h1 {
  font-family: nunito, sans-serif;
  font-size: 65px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 10px;
  color: var(--night-blue);
  text-transform: uppercase;
}
.notfound h3 {
  font-family: nunito, sans-serif;
  font-size: 25px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 10px;
  color: var(--night-blue);
  text-transform: uppercase;
}
.notfound p {
  width: 60%;
  font-family: nunito, sans-serif;
  color: var(--transparent2);
  font-weight: 400;
}
.notfound span {
  font-family: nunito, sans-serif;
  display: inline-block;
  font-weight: 700;
  border-radius: 40px;
  text-decoration: none;
  color: var(--red);
  cursor: pointer;
}

.pay {
  padding: 3%;
  min-height: 85vh;
}
.pay .error {
  width: 80%;
  padding: 3%;
  margin: auto;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  border-radius: 8px;
}
.pay .transaction {
  border: 5px solid var(--red);
  border-radius: 8px;
}
.pay .modal-head {
  padding: 3%;
  background: var(--red);
  font-size: 1.8em;
  font-weight: bolder;
  color: var(--white);
  text-align: center;
}
.content-modal h2 {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}
.icon-step {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  border-radius: 50%;
  background: var(--red);
  font-weight: bold;
  color: var(--white);
  transform: translate(-15px, 0px);
}
/* .content-modal h3 {
  text-align: justify;
} */
.content-modal h4 a {
  color: var(--blue);
  text-decoration: underline;
}
.phone-step {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  width: 80%;
  margin: auto;
  overflow: hidden;
  transition: all 2s ease-out;
}
.phone-step img {
  display: block;
  width: 100%;
}
.backfunds {
  display: flex;
  justify-content: center;
}
.pay .operation-id {
  margin: 15px 0px;
  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
}
.pay .operation-id h3 {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  margin: 10px 0px;
  color: var(--red);
}
.deco {
  display: inline-block;
  width: 10%;
  max-width: 100px;
  height: 10px;
  margin: 0px 2%;
  background: var(--red);
}


@media screen and (min-width: 768px) {
  .pay .error {
    width: 60%;
  }
  .pay .transaction {
    width: 80%;
    margin: auto;
  }
  .content-modal {
    padding: 2% 5%;
